.new {
  width: 100%;
  display: flex;

   @media (max-width: 768px){
      flex-direction: column;
   }

  .newContainer {
    
    flex: 6;

    @media (max-width: 768px){
        width: 100%;
    }

    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 10px;
      margin: 20px;
      display: flex;

      @media (max-width:768px) {
        flex-direction: column;
        margin: 10px;
        
      }

      h1 {
        color: lightgray;
        font-size: 19px;
      }

      .left {
        flex: 1;
        text-align: center;

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          object-fit: cover;
        }
      }

      .right {
        flex: 2;

        @media (max-width: 768px) {
          margin-top: 20px;
        }


        form {
          display: flex;
          flex-wrap: wrap;
          gap: 30px;
          justify-content: space-around;

          .formInput {
            width: 40%;

            @media (max-width: 768px){
              width: 100%;
            }

            label {
              display: flex;
              align-items: center;
              gap: 10px;

              .icon {
                cursor: pointer;
              }
            }

            input {
              width: 100%;
              padding: 5px;
              border: none;
              border-bottom: 1px solid gray;
            }
          }

          button {
            width: 150px;
            padding: 10px;
            border: none;
            background-color: teal;
            color: white;
            font-weight: bold;
            cursor: pointer;
            margin-top: 10px;

            @media (max-width:768px){
               width: 100%;
            }
          }
        }
      }
    }
  }
}


